/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Ubuntu", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  font-family: "Ubuntu", sans-serif;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Overpass", sans-serif;
  margin: 0 0 0 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  position: relative;
  background: linear-gradient(to top, hsl(353deg, 100%, 62%) 30%, hsl(13deg, 100%, 72%) 100%);
  border-radius: 0 0 0 10rem;
  min-height: 65rem;
}
.header .header-pattern {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../img/bg-pattern-intro-mobile.svg);
  border-bottom-left-radius: 10rem;
  background-position: right 65% bottom 67%;
  background-size: 130rem;
  background-repeat: no-repeat;
  opacity: 0.4;
}
@media (min-width: 1024px) {
  .header .header-pattern {
    background-position: right 70% bottom 50%;
    background-size: 250rem;
  }
}
.header .contenedor {
  position: relative;
}
.header .contenedor .header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.header .contenedor .header-content .nav-content,
.header .contenedor .header-content .header-info {
  display: flex;
  width: 100%;
  align-items: center;
}
.header .contenedor .header-content .nav-content {
  justify-content: space-between;
  padding: 5rem 2rem;
}
.header .contenedor .header-content .nav-content .logo {
  cursor: pointer;
}
.header .contenedor .header-content .nav-content .responsive-menu .icono-menu {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .header .contenedor .header-content .nav-content .responsive-menu {
    display: none;
  }
}
.header .contenedor .header-content .nav-content .menu {
  display: none;
}
@media (min-width: 1024px) {
  .header .contenedor .header-content .nav-content .menu {
    display: flex;
    width: 60%;
    font-size: 1.6rem;
  }
  .header .contenedor .header-content .nav-content .menu a {
    color: hsl(0deg, 0%, 100%);
    padding: 2rem 3rem;
  }
}
.header .contenedor .header-content .nav-content .login-logout-menu {
  display: none;
}
@media (min-width: 1024px) {
  .header .contenedor .header-content .nav-content .login-logout-menu {
    display: flex;
    justify-content: space-between;
  }
  .header .contenedor .header-content .nav-content .login-logout-menu :nth-child(odd) {
    color: hsl(0deg, 0%, 100%);
    padding: 2rem 2rem;
  }
  .header .contenedor .header-content .nav-content .login-logout-menu :nth-child(even) {
    margin: 0 1rem;
    font-weight: 700;
    color: hsl(355deg, 100%, 74%);
    background-color: hsl(0deg, 0%, 100%);
    padding: 2rem 3rem;
    border: 1px solid hsl(0deg, 0%, 100%);
    border-radius: 3rem;
  }
}
.header .contenedor .header-content .header-info {
  margin: 10rem;
  flex-direction: column;
}
.header .contenedor .header-content .header-info h1,
.header .contenedor .header-content .header-info p {
  text-align: center;
  color: hsl(0deg, 0%, 100%);
}
@media (min-width: 1024px) {
  .header .contenedor .header-content .header-info h1,
.header .contenedor .header-content .header-info p {
    font-size: 2rem;
  }
}
.header .contenedor .header-content .header-info h1 {
  font-size: 3.8rem;
}
@media (min-width: 1024px) {
  .header .contenedor .header-content .header-info h1 {
    font-size: 6rem;
  }
}
.header .contenedor .header-content .header-info .cta {
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin: 3rem 0;
}
@media (min-width: 1024px) {
  .header .contenedor .header-content .header-info .cta {
    width: 25%;
  }
}
.header .contenedor .header-content .header-info .cta .button {
  font-weight: 700;
  color: hsl(0deg, 0%, 100%);
  padding: 1.5rem 2rem;
  border: 1px solid hsl(0deg, 0%, 100%);
  border-radius: 3rem;
}
.header .contenedor .header-content .header-info .cta .button:first-child {
  background-color: hsl(0deg, 0%, 100%);
  color: hsl(355deg, 100%, 74%);
}